@keyframes move-up {
	0%{
		opacity: 0.6;
		top: -40px;
		transform: scale(0.9);
	}
	100%{
		opacity: 1;
		top: 0;
		transform: scale(1);
	}
}

@keyframes move-down {
	0%{
		opacity: 1;
		top: 0;
	}
	100%{
		opacity: 0.6;
		top: -40px;
	}
}

.slider__item {	
	position: relative;
	width: 560px;
	min-height: 240px;
	display: flex;
	align-items: flex-start;

	background: linear-gradient(180deg, #8CB928 0%, #45AE4D 100%);
	border-radius: 18px;
	color: #ffffff;
}

.slider__item.active {
	animation: move-up 0.5s;
}

.slider__item--notactive {
	animation: move-down 0.5s;	
	position: absolute;
	top: -40px;
	transform: scale(0.9);
	opacity: 0.6;
	z-index: -1;
}

.slider__item img {
	margin: 20px;
}

.slider__list {
	position: relative;
	@include list-clear();
	margin-top: 75px;
}

.slider__review-author {
	margin-top: 60px;
	margin-bottom: 0;
	@include font-set(18px, 21px);
	font-weight: 300;
}

.slider__review-description {
	margin-top: 20px;
	margin-bottom: 70px;
	width: 385px;
	@include font-set(20px, 23px);
}

.slider {
	position: relative;
}

.slider__button {
	position: absolute;
	outline: none;
	width: 20px;
	height: 20px;
	bottom: 20px;
	left: 50%;
	border: none;
	cursor: pointer;
	background-color: transparent;
	border: 4px solid #FFFFFF;
	transform: rotate(-45deg);
	border-top: none;
	border-right: none;

	&:hover {
		border-width: 5px;
		transform: scale(1.1) rotate(-45deg);
	}

	&:active {
		transform: translateY(5px) rotate(-45deg);
	}
}
