.feedback {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 870px;
	margin: 0 auto;
	margin-top: 110px;
	background-color: #ffffff;
	border-radius: 18px;
	box-shadow: 0px 35px 100px #48AE4B;
}

.feedback__description--sent {
	display: none;
	width: 550px;
	margin-top: 80px;
	margin-bottom: 0;
	@include font-set(36px, 42px);
	color: #313334;
	font-weight: bold;
	text-align: center;
}

.feedback__description {
	width: 360px;
	margin-top: 40px;
	margin-bottom: 0;
	text-align: center;
	@include font-set(20px, 23px);
	color: #313334;
	font-weight: bold;
}

.feedback__form {
	margin-top: 35px;
}

.form__field {
	margin: 0;
}

.form__phone {
	padding: 9px 24px;
	box-sizing: border-box;
	width: 280px;

	@include font-set(30px, 42px);
	color: #363636;
	font-weight: bold;

	border: 3px solid #DAE0E5;
	border-radius: 4px;
	outline: none;
}

.form__phone--notfilled:focus {
	border: 3px solid #47B247;
	box-shadow: 0px 24px 24px rgba(0, 152, 16, 0.25);
}

.form__phone--filled:focus {
	border: 3px solid #47B247;
	box-shadow: 0 0 0 #000000;
}

.feedback__submit {
	position: relative;
	margin-top: 40px;
	margin-bottom: 45px;
	padding: 17px 0;
	border: none;
	width: 280px;
	background-color: #72BF44;
	border-radius: 4px;
	cursor: pointer;

	@include font-set(22px, 26px);
	color: #ffffff;
	font-weight: bold;
	text-transform: uppercase;
	font-family: 'Roboto Condensed', sans-serif;

	&::after {
		content: "";
		position: absolute;
		top: 60px;
		right: 10px;
		width: 260px;
		height: 6px;
		background-color: #009810;
		border-radius: 0px 0px 4px 4px;
	}

	&:active {
		transform: translateY(5px);
	}

	&:active::after {
		display: none;
	}
}

.feedback__submit--disabled {
	margin-top: 15px;
	background-color: #DAE0E5;

	&::after {
		display: none;
	}
}

.feedback__submit span {
	position: relative;
	padding-right: 40px;

	&::after {
		content: "";
		position: absolute;
		top: 0;
		right: -10px;
		width: 42px;
		height: 28px;
		background-image: url("../img/ic-send.svg");
	}
}

.feedback--sent .feedback__description {
	display: none;
}

.feedback--sent .form__phone {
	display: none;
}

.feedback--sent .feedback__description--sent {
	display: block;
}
