.popup {
	display: none;
	width: 720px;
	position: relative;

	background: linear-gradient(180deg, #8CB928 0%, #45AE4D 100%);
	border-radius: 18px;
	border: 1px solid #888888;
}

@keyframes popup-move-left {
	0%{
		opacity: 0.6;
		left: 0;
	}
	100%{
		opacity: 1;
		left: 50%;
	}
}

@keyframes popup-move-right {
	0%{
		opacity: 0.6;
		left: 100%;
	}
	100%{
		opacity: 1;
		left: 50%;
	}
}

.popup--opened {
	display: block;
  	position: fixed;
  	top: 50%;
  	left: 50%;
  	transform: translate(-50%, -50%);
}

.device-popup.popup--opened {
	animation: popup-move-left 0.5s;
}

.phone-popup.popup--opened {
	animation: popup-move-right 0.5s;
}

.popup__features-list {
	@include list-clear();
	display: flex;
	flex-wrap: wrap;
	padding-left: 40px;
	padding-right: 40px;
	padding-bottom: 40px;
	padding-top: 25px;
}

.popup__features-item {
	display: flex;
	width: 310px;
	padding: 35px 18px;
	margin-top: 15px;
	box-sizing: border-box;

	background-color: #ffffff;
	border-radius: 18px;

	&:not(:nth-child(2n)) {
		margin-right: 18px;
	}
}

.popup__features-container {
	width: 126px;
	height: 126px;
	background: linear-gradient(180deg, #8CB928 0%, #45AE4D 100%);
	border-radius: 50%;
}

.popup__features-icon {
	width: 100%;
	height: 100%;
	
	background-repeat: no-repeat;
	background-position: center;
}

.device-popup .popup__features-icon-1 {
	background-image: url("../img/feat-icon_1.png");
}

.device-popup .popup__features-icon-2 {
	background-image: url("../img/feat-icon_2.png");
}

.device-popup .popup__features-icon-3 {
	background-image: url("../img/feat-icon_3.png");
}

.device-popup .popup__features-icon-4 {
	background-image: url("../img/feat-icon_4.png");
}

.device-popup .popup__features-icon-5 {
	background-image: url("../img/feat-icon_5.png");
}

.phone-popup .popup__features-icon-1 {
	background-image: url("../img/feat-icon_6.png");
}

.phone-popup .popup__features-icon-2 {
	background-image: url("../img/feat-icon_7.png");
}

.phone-popup .popup__features-icon-3 {
	background-image: url("../img/feat-icon_8.png");
}

.phone-popup .popup__features-icon-4 {
	background-image: url("../img/feat-icon_9.png");
}

.phone-popup .popup__features-icon-5 {
	background-image: url("../img/feat-icon_10.png");
}

.phone-popup .popup__features-icon-6 {
	background-image: url("../img/feat-icon_11.png");
}

.popup__features-text {
	width: 130px;
	margin-left: 10px;

	@include font-set(20px, 23px);
	font-weight: bold;
}

.popup__close-btn {
	width: 52px;
	height: 52px;
	position: absolute;

	border: none;
	outline: none;
	cursor: pointer;
	border-radius: 50%;
	background: linear-gradient(180deg, #8CB928 0%, #45AE4D 100%);

	&::after {
		content: "";
		position: absolute;
		width: 22px;
		height: 22px;
		top: 15px;
		left: 15px;
		background-image: url("../img/close.svg");
		background-position: center;
	}

	&:hover::after {
		transition: 0.3s;
		transform: scale(1.1) rotate(90deg);
	}
}

.device-popup .popup__close-btn {
	top: 45%;
	left: -85px;
}

.phone-popup .popup__close-btn {
	top: 45%;
	right: -85px;
}
