.features {
	margin-top: 85px;
}

.features__list {
	@include list-clear();
	margin: 0 auto;
	width: 870px;
	display: flex;
}

.features__item:not(:nth-child(2n)) {
	margin-right: 90px;
}

.features__image {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 390px;
	height: 620px;

	border-radius: 18px;
	filter: drop-shadow(0px 20px 30px rgba(71, 178, 71, 0.5));
	background-image: linear-gradient(180deg, #8CB928 0%, #45AE4D 100%);
}

.features__item {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.features__button {
	margin-top: 15px;
	width: 260px;
	border: none;
	outline: none;
	cursor: pointer;
	background-color: transparent;

	@include font-set(20px, 23px);
	text-transform: uppercase;
	font-weight: bold;
	color: #747474;
	filter: drop-shadow(0px 10px 20px #47B247);
}