body {
	font-family: 'Roboto', sans-serif;
	background-color: #ffffff;
}

.wrapper {
	width: 960px;
	margin: 0 auto;
}

a {
	text-decoration: none;
}

.visually-hidden {
  	position: absolute;
  	clip: rect(1px 1px 1px 1px);
  	padding: 0;
  	border: 0;
  	height: 1px;
  	width: 1px;
  	overflow: hidden;
}