.page-header {
	background-image: url("../img/bg-1.jpg");
	background-repeat: no-repeat;
	background-position: center;
	color: #000000;
}

.page-header__container {
	padding-top: 55px;
	padding-left: 45px;
	padding-bottom: 45px;
	width: 270px;
}

.page-header__logo {
	display: flex;
}

.page-header__title {
	margin-top: 10px;
	margin-left: 17px;
	margin-bottom: 0;

	@include font-set(32px, 34px);
	font-weight: bold;
	color: #000000;
}

.page-header__description {
	margin-top: 110px;

	@include font-set(20px, 23px);
	font-weight: bold;
}