.price {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 70px;
}

.price__title {
	margin: 0;
	@include font-set(36px, 42px);
	font-family: 'Roboto Condensed', sans-serif;
	font-weight: bold;
}

.price__list {
	@include list-clear();
	margin-top: 40px;
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.price__link {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 214px;
	height: 214px;
	
	border-radius: 18px;
	background: linear-gradient(180deg, #8CB928 0%, #45AE4D 100%);

	&:hover {
		transition-duration: 0.3s;
		transform: scale(1.05);
	}
}

.price__link-image {
	width: 100%;
	height: 140px;
	background-repeat: no-repeat;
	background-position: center bottom;
}

.price__link-image--buy {
	background-image: url("../img/buy-icon.png");
}

.price__link-image--inst {
	background-image: url("../img/ras-icon.png");
}

.price__link-image--control {
	background-image: url("../img/control-icon.png");
}

.price__link-image--1500 {
	background-image: url("../img/1500-icon.png");
}

.price__description {
	margin-top: 20px;
	margin-bottom: 0;
	text-align: center;

	@include font-set(20px, 23px);
	color: #ffffff;
	font-weight: bold;
}

.price__item:nth-child(3) .price__description {
	margin-top: 10px;
}

.price__item:nth-child(4) .price__description {
	margin-top: 10px;
}

.price__description span {
	@include font-set(19px, 21px);
}

.price__item {
	position: relative;
}

.price__shadow {
	z-index: -1;
	position: absolute;
	top: 0;
	right: 37px;
	width: 140px;
	height: 100%;
	box-shadow: 0px 25px 100px #48AE4B;
}